.app {
  border: 1px solid #b9b9b9;
  flex-direction: column;
  gap: 10px;
  width: 350px;
  margin: 20px;
  padding: 20px;
  display: flex;
}

.app__error-text {
  color: red;
}

.app__image {
  width: 100%;
  height: auto;
}

.app-button {
  background: linear-gradient(#fff, #f0f0f0);
  border: 1px solid #b9b9b9;
  border-radius: 1px;
  padding: 8px;
}

.app-button:hover, .app-button:disabled {
  background: linear-gradient(#fff, #f0f0f050);
}

.checkbox-item {
  flex-direction: row;
  gap: 4px;
  display: flex;
}

body {
  white-space: pre-line;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  flex-direction: column;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-size: 14px;
  display: flex;
}

a {
  -webkit-tap-highlight-color: transparent;
}

body, button, p, h1, h2, h3, h4, h5, h6, input {
  margin: 0;
  padding: 0;
}

button {
  cursor: pointer;
  user-select: none;
  font-size: unset;
  -webkit-tap-highlight-color: transparent;
  background: none;
  border: none;
  text-decoration: none;
  font-family: unset !important;
}

button:disabled {
  cursor: default;
}
